<!--
 * @Descripttion: 工具提醒
 * @version:
 * @Author: Lois
 -->
<template>
<div>
  <sHeader :titleName="WORK_STATION.TOOLS_REPLACE">
    <div slot="header-action" class="head-operation">
      <HeaderAction name="1" @onOprationItem="goHome()">
        <el-button slot="action" >
          <i class="el-icon-d-arrow-left"></i>{{BTN_TEXT.RETURN}}
        </el-button>
      </HeaderAction>
      <HeaderAction name="1" @onOprationItem="showCreateNewToolModal()" v-if="selectData.code">
        <el-button slot="action" >
          <i class="el-icon-plus"></i>{{EQUIPMENT_DATA.ADD_TOOL}}
        </el-button>
      </HeaderAction>
    </div>
  </sHeader>
  <el-container>
    <el-main class="view">
      <el-row v-if="!addTool.show&&!toolHistory.show&&!toolDetail.show&&!moveToolPage.show">
         <el-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6" class="tool-area left" >
           <div class="tool-content">
             <HeaderTitle class="header" :titleName="EQUIPMENT_DATA.GROUP_LIST"></HeaderTitle>
           <div class="workshop-tree">
            <ZTree :nodes="nodeData" :isExpendAll="true" @ztreeOnClick="handleNodeClick"></ZTree>
           </div>
           </div>
         </el-col>
         <el-col :xs="18" :sm="18" :md="18" :lg="18" :xl="18" class="tool-area right" style="padding-left: 0">
           <div class="tool-content">
             <HeaderTitle class="header" :titleName="EQUIPMENT_DATA.CONTAIN_TOOL_LIST + ' '+ selectData.name ">
              <!-- <div slot="head-operation" class="head-operation">
                <HeadOperationItem name="historyData" @onOprationItem="showToolHistoryPage()" v-if="selectData.code">
                  <i slot="operation-icon" class="icon-add"></i>
                  <span slot="operation-name">{{EQUIPMENT_DATA.TOOL_HISTORY_DATA}}</span>
                </HeadOperationItem>
              </div> -->
             </HeaderTitle>
            <el-row>
              <el-col :span="24">
                <VXETable
                    v-if="toolListTable"
                    :tableName="'test'"
                    :tableLoading="tableLoading"
                    :pager-config="toolListTable.page"
                    :tableDatas="toolListTable.data"
                    :tableColumns="toolListTable.columns"
                    :tablePage="toolListTable.page"
                    :checkRecords="selectRecords"
                    @onTablePageChange="handleToolListTablePageChange"
                    @chooseCheck="checkboxChangeEvent"
                    :checkboxConfig="toolListTable.checkboxConfig"
                    ref="toolListGrid"
                  >
                    <div slot="tableOperation" class="tableOperations">
                      <div class="tableOperation-item">
                        <HeadOperationItem name="batchDeleteStaff" @onOprationItem="stopSelectedTool()">
                          <i slot="operation-icon" class="icon-batchDelete"></i>
                          <span slot="operation-name">{{BTN_TEXT.STOP_ALL}}</span>
                        </HeadOperationItem>
                      </div>
                    </div>
                </VXETable>
              </el-col>
            </el-row>
           </div>
         </el-col>
      </el-row>
      <!-- 添加工具部分 -->
      <el-row v-if="addTool.show">
        <el-col :span="24">
          <vxe-table
          class="fix-mg-table"
          ref="adTable"
          border
          show-header-overflow
          show-overflow
          highlight-hover-row
          :data="addTool.data"
          :edit-config="{trigger: 'click', mode: 'row'}">
            <vxe-table-column field="toolTypeName" :title="EQUIPMENT_DATA.TOOL_TYPE" :edit-render="{name: 'input', attrs: {type: 'text'}}">
              <template v-slot="{ row }">
                {{ row.toolTypeName }}
              </template>
              <template v-slot:edit="scope">
                <el-select v-model="scope.row.toolTypeName" :placeholder="MSG.PLEASE_CHOOSE" @change="handleToolTypeChange(scope.row)">
                  <el-option
                    v-for="item in $store.state.filters.toolTypeList"
                    :key="item.basicValue"
                    :label="item.basicValue"
                    :value="item.basicValue">
                  </el-option>
                </el-select>
              </template>
            </vxe-table-column>
            <vxe-table-column field="toolName" :title="EQUIPMENT_DATA.TOOL_NAME" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="toolCode" :title="EQUIPMENT_DATA.TOOL_CODE" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="toolModel" :title="EQUIPMENT_DATA.TOOL_TYPE_CODE" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="initialLife" :title="EQUIPMENT_DATA.SET_TOOL_START_AGE" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="toolMaxLife" :title="EQUIPMENT_DATA.SET_TOOL_MAX_AGE" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column field="manufacturer" :title="EQUIPMENT_DATA.TOOL_FACTORY" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
            <vxe-table-column >
              <template v-slot:header>
                <div>
                  {{BTN_TEXT.OPERATION}}
                </div>
              </template>
              <template v-slot="{ row }">
                <el-button @click.native.prevent type="text" size="small" @click="removeAddItems(row)">{{BTN_TEXT.DELETE}}</el-button>
              </template>
            </vxe-table-column>
          </vxe-table>
        </el-col>
        <el-col :span="24" class="actions">
            <el-button type="primary" @click="saveAddToolData()">{{BTN_TEXT.SAVE}}</el-button>
            <el-button  @click="closeAddToolPage()">{{BTN_TEXT.RETURN}}</el-button>
        </el-col>
      </el-row>
      <el-row v-if="toolHistory.show">
        <toolHistory @goBack="goBack" :code="selectData.code"></toolHistory>
      </el-row>
      <el-row v-if="toolDetail.show">
        <toolDetail @goBack="goBack" :toolId="selectTool.toolId"></toolDetail>
      </el-row>
      <el-row v-if="moveToolPage.show">
        <moveTool @goBack="goBack"></moveTool>
      </el-row>
    </el-main>
    <!-- 修改工具modal -->
    <modify-tool-modal
      :showDialog="showModifyTool"
      :resOrigData="selectTool"
      :title="EQUIPMENT_DATA.UPDATE_TOOL_ACTION"
      :handleCancel="clickShowModifyToolCancel"
      :handleConfirm="saveModifyTool"
      :handleClose="clickShowModifyToolCancel"
    />
    <create-new-tool-modal
      :showDialog="showCreateNewTool"
      :title="EQUIPMENT_DATA.ADD_TOOL"
      :handleCancel="clickShowCreateNewToolCancel"
      :handleConfirm="saveCreateNewTool"
      :handleClose="clickShowCreateNewToolCancel"
      :extendPropertyValueVoList="createExtendPropertyValueVoList"
    />
  </el-container>
</div>
</template>

<script>
import js from '@/assets/js/workStation/toolNotify.js';
export default js;
</script>
<style lang="less" scoped>
@import '../../../assets/less/workStation/toolNotify.less';
</style>
