// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
import ModifyToolModal from '@/components/modal/ToolModal/modifyToolModal';
import CreateNewToolModal from '@/components/modal/ToolModal/createNewToolModal';
import ZTree from '@/components/common/ZTree/ZTree';
// 移动工具组件
import moveTool from '@/views/equipment/ToolNotify/moveTool.vue';
// 设备详情数据
import toolDetail from '@/views/equipment/ToolNotify/toolDetail.vue';
// 设备历史数据
import toolHistory from '@/views/equipment/ToolNotify/toolHistory.vue';
// API
import {
  getTreeByUser,
  getPageToolByDeviceCode,
  getListToolType,
  insertOrUpdateBatchTool,
  updateBatchStopTool,
  updateScrappedTool,
  updateEnableTool,
  updateToolById,
  updateTool
} from '@/api/equipment.js';
// 引入警告信息
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
// 导入日期处理类库
import moment from 'moment';
import {
  EQUIPMENT_DATA,
  BTN_TEXT,
  MSG,
  TITLE,
  STATUS_CODE,
  TOOL_STATUS,
  WORK_STATION,
  DEVICE_CHECK
} from '@/utils/constantsFile';
import sHeader from '@/components/common/StationHeader/header.vue';
import HeaderAction from '@/components/common/StationHeader/headerAction.vue';
import {
  extendPropertyGetListAction,
  extendPropertyValueInsertOrUpdateBatchAction
} from '@/api/extendConfig';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable,
    // 移动工具组件
    moveTool,
    // 设备详情数据
    toolDetail,
    // 设备历史数据
    toolHistory,
    ModifyToolModal,
    CreateNewToolModal,
    ZTree,
    sHeader,
    HeaderAction
  },
  data() {
    const checCheckboxkMethod = ({ row }) => {
      return parseInt(row.toolState) !== 2;
    };
    return {
      checCheckboxkMethod: checCheckboxkMethod,
      WORK_STATION: WORK_STATION,
      TOOL_STATUS: TOOL_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      MSG: MSG,
      title: EQUIPMENT_DATA.TOOL_NOTIFY,
      extendPropertyValueVoList: [],
      createExtendPropertyValueVoList: [],
      toolListTable: {
        checkboxConfig: {
          checkMethod: checCheckboxkMethod
        },
        page: {
          currentPage: 1,
          pageSize: 10,
          total: 30,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: [
            'Sizes',
            'PrevJump',
            'PrevPage',
            'Number',
            'NextPage',
            'NextJump',
            'FullJump',
            'Total'
          ]
        },
        columns: [
          { type: 'checkbox', width: '50', align: 'center', fixed: 'left' },
          {
            field: 'toolName',
            title: EQUIPMENT_DATA.TOOL_NAME,
            width: '150',
            align: 'center',
            className({ row }) {
              var classnm = null;
              if (row.isWarning === 1) {
                classnm = 'red-font';
              }
              return classnm;
            }
          },
          {
            field: 'recordTypeName',
            title: '计数方式',
            width: '150',
            align: 'center'
          },
          {
            field: 'toolModel',
            title: EQUIPMENT_DATA.TOOL_TYPE_CODE,
            width: '150',
            align: 'center',
            className({ row }) {
              var classnm = null;
              if (row.isWarning === 1) {
                classnm = 'red-font';
              }
              return classnm;
            }
          },
          {
            field: 'toolStateName',
            title: EQUIPMENT_DATA.STATUS,
            width: '150',
            align: 'center',
            className({ row }) {
              var classnm = null;
              if (row.isWarning === 1) {
                classnm = 'red-font';
              }
              return classnm;
            }
          },
          {
            field: 'toolMaxLife',
            title: EQUIPMENT_DATA.TOOL_MAX_AGE,
            width: '150',
            align: 'center',
            formatter: ({ row }) => {
              return this.tranfromTime(row, 'toolMaxLife');
            },
            className({ row }) {
              var classnm = null;
              if (row.isWarning === 1) {
                classnm = 'red-font';
              }
              return classnm;
            }
          },
          {
            field: 'overLife',
            title: EQUIPMENT_DATA.TOOL_REMAIN_AGE,
            width: '150',
            align: 'center',
            formatter: ({ row }) => {
              return this.tranfromTime(row, 'overLife');
            },
            className({ row }) {
              var classnm = null;
              if (row.isWarning === 1) {
                classnm = 'red-font';
              }
              return classnm;
            }
          },
          {
            field: 'useCount',
            title: EQUIPMENT_DATA.USED_TIME,
            width: '150',
            align: 'center',
            formatter: ({ row }) => {
              return this.tranfromTime(row, 'useCount');
            },
            className({ row }) {
              var classnm = null;
              if (row.isWarning === 1) {
                classnm = 'red-font';
              }
              return classnm;
            }
          },
          {
            field: 'updateTime',
            title: EQUIPMENT_DATA.UPDATE_TIME,
            width: '200',
            align: 'center',
            formatter({ cellValue, row, column }) {
              if (cellValue) {
                return moment(cellValue).format('YYYY-MM-DD HH:mm:ss');
              } else {
                return '';
              }
            },
            className({ row }) {
              var classnm = null;
              if (row.isWarning === 1) {
                classnm = 'red-font';
              }
              return classnm;
            }
          },
          {
            field: '',
            title: BTN_TEXT.OPERATION,
            width: '250',
            fixed: 'right',
            align: 'center',
            slots: {
              default: ({ row, column }) => {
                var buttonList = [
                  <el-button
                    onClick={() => this.showModifyToolModal(row)}
                    type="text"
                  >
                    {BTN_TEXT.MODIFY}
                  </el-button>
                ];
                if (parseInt(row.toolState) !== 3) {
                  buttonList.push(
                    <el-button
                      type="text"
                      onClick={() => this.confirmScrappedTool(row.toolId)}
                    >
                      {BTN_TEXT.SCRAP}
                    </el-button>
                  );
                }
                if (parseInt(row.toolState) !== 2) {
                  buttonList.push(
                    <el-button type="text" onClick={() => this.stopTool([row])}>
                      {BTN_TEXT.STOP}
                    </el-button>
                  );
                }
                if (parseInt(row.toolState) === 2) {
                  buttonList.push(
                    <el-button type="text" onClick={() => this.enableTool(row)}>
                      {BTN_TEXT.TURN_ON}
                    </el-button>
                  );
                  buttonList.push(
                    <el-button
                      onClick={() => this.showMoveToolPage(row)}
                      type="text"
                    >
                      {BTN_TEXT.MOVE}
                    </el-button>
                  );
                }
                return buttonList;
              },
              header: ({ column }) => {
                return [
                  <span>
                    <span>{column.title}</span>
                    <i class="el-icon-s-tools set-btn"></i>
                  </span>
                ];
              }
            }
          }
        ],
        data: []
      }, // 所属工具列表
      tableLoading: false,
      selectRecords: [],
      addTool: {
        show: false,
        title: EQUIPMENT_DATA.ADD_TOOLS,
        data: []
      },
      moveToolPage: {
        show: false,
        title: EQUIPMENT_DATA.SELECT_TOOL
      },
      toolHistory: {
        show: false,
        title: EQUIPMENT_DATA.TOOL_HISTROY_DATAS
      },
      toolDetail: {
        show: false,
        title: EQUIPMENT_DATA.TOOL_INFO_DATAS
      },
      defaultPageTitle: EQUIPMENT_DATA.TOOL_NOTIFY,
      // others
      nodeData: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      // 当前选中的设备
      selectData: {
        name: ''
      },
      // 当前选中的工具 用于看详情
      selectTool: {},
      showGroup: false,
      showCreate: false,
      showToolHistory: false,
      showModifyTool: false,
      showCreateNewTool: false
    };
  },
  methods: {
    /**
   * @description 根据时间单位  转换数据
  */
    tranfromTime(row, name) {
      let count = '';
      if (row.recordType === 2) {
        if (row.timeRecordUnit === 1) {
          count = row[name];
        } else if (row.timeRecordUnit === 2) {
          count = (row[name] / 60).toFixed(2);
        } else if (row.timeRecordUnit === 3) {
          count = (row[name] / 3600).toFixed(2);
        };
      } else {
        count = row[name];
      }
      return count;
    },
    /**
     * @description: 初始化界面
     * @return: void
     */
    initPage() {
      this.getTree();
    },
    /**
     * @description: 获取树结构
     * @return: void
     */
    getTree() {
      getTreeByUser()
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.nodeData = res.rows;
            for (var i = 0; i < this.nodeData.length; i++) {
              if (this.nodeData[i].code) {
                this.selectData = this.nodeData[i];
                break;
              }
            }
            this.getToolListData(this.selectData.code);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 树点击事件
     * @param {object} data 树结构渲染数据
     * @param {object} treeNode 树结构真实数据
     * @return: void
     */
    handleNodeClick(data, treeNode) {
      if (treeNode.code) {
        this.selectData = treeNode;
        this.showGroup = true;
        this.getToolListData(this.selectData.code);
      } else {
        showWarningMessage(DEVICE_CHECK.PLEASE_CHOOSE_DEVICE);
      }
    },
    // *********所属工具列表更新***********
    /**
     * @description: 获取工具列表数据
     * @param {string} code 工具代码
     * @return: void
     */
    getToolListData(code) {
      var self = this;
      this.selectRecords = [];
      this.tableLoading = true;
      var params = {
        current: this.toolListTable.page.currentPage,
        size: this.toolListTable.page.pageSize,
        deviceCode: code
      };
      getPageToolByDeviceCode(params, { condition: {} })
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            self.toolListTable.data = res.rows;
            self.toolListTable.page.total = res.total;
            self.tableLoading = false;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 执行table分页
     * @param {string} currentPage 第几页
     * @param {string} pageSize 一页几条
     * @return: void
     */
    handleToolListTablePageChange(currentPage, pageSize) {
      this.toolListTable.page.currentPage = currentPage;
      this.toolListTable.page.pageSize = pageSize;
      this.getToolListData(this.selectData.code);
    },
    /**
     * @description: table各种事件
     * @return: void
     */
    checkboxChangeEvent(records) {
      this.selectRecords = records;
    },
    /**
     * @description: 批量暂停
     * @return: void
     */
    stopSelectedTool() {
      if (this.selectRecords.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_ACTION);
      } else {
        this.$confirm(MSG.CONFIRM_CHOOSE_STOP, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.stopTool(this.selectRecords);
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: MSG.CANCELED_CHOOSE_STOP
            });
          });
      }
    },
    /**
     * @description: 暂停工具
     * @param {object} data 选中数据
     * @return: void
     */
    stopTool(data) {
      var ids = [];
      data.forEach(function(item) {
        ids.push(item.toolId);
      });
      updateBatchStopTool(null, ids)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getToolListData(this.selectData.code);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 启用工具
     * @param {object} data 选中数据
     * @return: void
     */
    enableTool(data) {
      updateEnableTool({
        toolId: data.toolId
      })
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getToolListData(this.selectData.code);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 确认是否要报废工具
     * @param {string} id 选中工具id
     * @return: void
     */
    confirmScrappedTool(id) {
      this.$confirm(MSG.CONFIRM_TOOL_SCRAPPED, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.SCRAPPED,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.scrappedTool(id);
        })
        .catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: MSG.CONFIRM_TOOL_SCRAPPED
          // });
        });
    },
    /**
     * @description: 报废工具
     * @param {string} id 选中工具id
     * @return: void
     */
    scrappedTool(id) {
      updateScrappedTool({ toolId: id })
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getToolListData(this.selectData.code);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 保存添加的工具
     * @return: void
     */
    saveCreateNewTool(confirmdata) {
      var data = JSON.parse(JSON.stringify(confirmdata));
      this.clickShowCreateNewToolCancel();
      data.deviceCode = this.selectData.code;
      insertOrUpdateBatchTool(null, [data])
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            if (data.extendPropertyValueVoList.length > 0) {
              this.saveExtendValue(
                data.extendPropertyValueVoList,
                res.rows[0].toolId
              );
            } else {
              this.getToolListData(this.selectData.code);
            }
            this.getToolListData(this.selectData.code);
            showSuccessMessage(res.message);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 改工具操作
     * @param {object} data 选中工具
     * @return: void
     */
    showModifyToolModal(data) {
      this.selectTool = data;
      this.showModifyTool = true;
    },
    /**
     * @description: 保存修改工具参数
     * @param {object} data 选中工具
     * @return: void
     */
    saveModifyTool(confirmdata) {
      var data = JSON.parse(JSON.stringify(confirmdata));
      var params = {
        toolId: data.toolId,
        recordProductionCount: data.recordProductionCount,
        initialLife: data.initialLife,
        toolMaxLife: data.toolMaxLife
      };
      this.clickShowModifyToolCancel();
      updateTool(params)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            if (data.extendPropertyValueVoList) {
              this.saveExtendValue(
                data.extendPropertyValueVoList,
                res.rows.toolId
              );
            } else {
              this.getToolListData(this.selectData.code);
            }
            showSuccessMessage(res.message);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 关闭修改工具modal
     * @return: void
     */
    clickShowModifyToolCancel: function() {
      this.showModifyTool = false;
    },
    // **********工具转移功能**********
    /**
     * @description: 显示工具转移页面
     * @param {object} data 选中工具
     * @return: void
     */
    showMoveToolPage: function(data) {
      this.selectTool = data;
      this.moveToolPage.show = true;
      this.title = this.moveToolPage.title;
    },
    /**
     * @description: 关闭工具转移页面
     * @return: void
     */
    closeMoveToolPage: function(deviceCode) {
      this.moveToolPage.show = false;
      this.title = this.defaultPageTitle;
      // 转移 工具
      updateToolById({
        toolId: this.selectTool.toolId,
        deviceCode: deviceCode
      })
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.getToolListData(this.selectData.code);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 打开添加工具对话框
     * @return: void
     */
    showCreateNewToolModal() {
      getListToolType()
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            var result = {
              name: 'toolTypeList',
              data: res.rows
            };
            this.$store.commit('setFiltersData', result);
            this.createExtendPropertyValueVoList = this.extendPropertyValueVoList;
            this.showCreateNewTool = true;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    },
    /**
     * @description: 关闭添加工具对话框
     * @return: void
     */
    clickShowCreateNewToolCancel: function() {
      this.showCreateNewTool = false;
    },
    /**
     * @description: 后退
     * @return: void
     */
    goBack(val) {
      // 显示组件
      if (val.page === 'movetool') {
        this.closeMoveToolPage();
        this.closeMoveToolPage(val.data.row.deviceCode);
      }
      if (val.page === 'toolhistory') {
        this.closeToolHistoryOrDetailPage();
      }
    },
    /**
     * @description: 返回首页
     * @return void
     */
    goHome() {
      this.$router.replace('/workstation/home');
    },
    async setTableColumn() {
      try {
        const res = await extendPropertyGetListAction(
          {},
          {
            conditionJson1: [
              {
                column: 'extendType',
                conditionJson1: [],
                conditionJson2: [],
                current: 0,
                operator: 1,
                operatorGroup: 0,
                orderBy: {},
                ptype: 'int',
                size: 0,
                type: 'eq',
                value: 5 // 工具提醒的模板id
              }
            ]
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.extendPropertyValueVoList = res.rows;
          var tableColumns = [];
          var tableColumnsHead = this.toolListTable.columns.slice(
            0,
            this.toolListTable.columns.length - 1
          );
          var tableColumnsOperation = this.toolListTable.columns[
            this.toolListTable.columns.length - 1
          ];
          tableColumns = tableColumns.concat(tableColumnsHead);
          if (this.extendPropertyValueVoList.length > 0) {
            const arr = this.extendPropertyValueVoList;
            for (var i = 0; i < arr.length; i++) {
              tableColumns.push({
                field: arr[i].propertyCode,
                title: arr[i].extendName,
                width: '160',
                align: 'center',
                formatter: this.$options.filters.formatterExtendValue
              });
            }
          }
          tableColumns = tableColumns.concat(tableColumnsOperation);
          this.toolListTable.columns = tableColumns;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 保存扩展字段数据
     * @return: void
     */
    saveExtendValue(data, id) {
      var arr = [];
      data.forEach(function(item) {
        var json = {
          extendId: item.extendId,
          extendValue: item.extendValue,
          propertyCode: item.propertyCode,
          relationshipTypeId: id,
          extendValueId: item.extendValueId
        };
        arr.push(json);
      });
      extendPropertyValueInsertOrUpdateBatchAction({}, arr)
        .then((res) => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.getToolListData(this.selectData.code);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch((err) => {
          showErrorNotify(err);
        });
    }
  },
  created() {
    this.setTableColumn();
    this.initPage();
  }
};
