<template>
  <el-dialog
    class="create-tool"
    width="50%"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content">
      <el-row>
        <el-form
            ref="updateForm"
            :label-width="formLabelWidth"
            :model="data"
            :rules="rulesUpdate"
            class="">
            <el-form-item :label="EQUIPMENT_DATA.TOOL_TYPE" prop="toolTypeName">
              <el-select v-model="data.toolTypeName" :placeholder="MSG.PLEASE_CHOOSE" @change="handleToolTypeChange(data)">
                <el-option
                  v-for="item in $store.state.filters.toolTypeList"
                  :key="item.basicValue"
                  :label="item.basicValue"
                  :value="item.basicValue">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item :label="EQUIPMENT_DATA.TOOL_NAME" prop="toolName">
              <el-input placeholder v-model="data.toolName"></el-input>
            </el-form-item>
            <el-form-item :label="EQUIPMENT_DATA.TOOL_CODE" prop="toolCode">
              <el-input placeholder v-model="data.toolCode"></el-input>
            </el-form-item>
            <el-form-item :label="EQUIPMENT_DATA.TOOL_TYPE_CODE" prop="toolModel">
              <el-input placeholder v-model="data.toolModel"></el-input>
            </el-form-item>
            <el-form-item :label="EQUIPMENT_DATA.SET_TOOL_START_AGE" prop="initialLife">
              <el-input placeholder v-model="data.initialLife" @keyup.native="removeSpace('initialLife')"></el-input>
            </el-form-item>
            <el-form-item :label="EQUIPMENT_DATA.SET_TOOL_MAX_AGE" prop="toolMaxLife">
              <el-input placeholder v-model="data.toolMaxLife" @keyup.native="removeSpace('toolMaxLife')"></el-input>
            </el-form-item>
            <el-form-item :label="EQUIPMENT_DATA.TOOL_FACTORY" prop="manufacturer">
              <el-input placeholder v-model="data.manufacturer"></el-input>
            </el-form-item>
            <el-form-item label="计数方式" prop="recordType">
              <el-select v-model="data.recordType" placeholder="请选择计数方式">
                <el-option :label="yieldKey + '计数'" value="1"></el-option>
                <el-option label="时间计数" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="时长单位" prop="timeRecordUnit" v-if="data.recordType == 2">
              <el-select v-model="data.timeRecordUnit" placeholder="请选择时长单位">
                <el-option label="秒" value="1"></el-option>
                <el-option label="分" value="2"></el-option>
                <el-option label="时" value="3"></el-option>
              </el-select>
            </el-form-item>
            <div v-if="showValidate && extendPropertyValueVoList && extendPropertyValueVoList.length>0">
              <div v-for="(item,index) in extendPropertyValueVoList" :key="index">
                <el-form-item :class="{'requiredStar':item.propertyIsNull===1}" :label="item.extendName" :label-width="formLabelWidth" :prop="'extendPropertyValueVoList.'+index+'.extendValue'" :rules="rulesUpdate[item.propertyCode]" >
                  <el-input v-if="item.propertyType!==3" v-model="item.extendValue" @input="change($event)"></el-input>
                  <el-date-picker
                   @input="change($event)"
                    v-else
                    v-model="item.extendValue"
                    type="datetime"
                    :format="FORM_CONFIG_DATE[item.propertyLength]"
                    :value-format="FORM_CONFIG_DATE[item.propertyLength]"
                  ></el-date-picker>
                </el-form-item>
              </div>
            </div>
         </el-form>
      </el-row>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="checkDataCorrect">{{confirmLabel}}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
    </div>
  </el-dialog>
</template>
<script>
// 文字常量
import { EQUIPMENT_DATA, BTN_TEXT, MSG, FORM_CONFIG_DATE } from '@/utils/constantsFile.js';
export default {
  name: 'CreateNewToolModal',
  props: {
    // 模态框名字
    title: { type: String, default: '' },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function,
    // 扩展字段
    extendPropertyValueVoList: { type: Array, default: () => [] }
  },
  data() {
    var setValidateForm = (rule, value, callback) => {
      return this.$options.filters.formatterFormValidate(rule, value, callback, this.extendPropertyValueVoList);
    };
    return {
      setValidateForm: setValidateForm,
      BTN_TEXT: BTN_TEXT,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      MSG: MSG,
      showValidate: false,
      rulesUpdate: {
        toolTypeName: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        toolName: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }, { min: 1, max: 20, message: MSG.CONTENT_LIMITIATION }],
        toolCode: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 1, max: 20, message: MSG.CONTENT_LIMITIATION }
        ],
        toolModel: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 1, max: 20, message: MSG.CONTENT_LIMITIATION }
        ],
        initialLife: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 0, max: 6, message: MSG.CONTENT_LIMITIATION, trigger: 'blur' }],
        toolMaxLife: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 0, max: 6, message: MSG.CONTENT_LIMITIATION, trigger: 'blur' }],
        manufacturer: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 1, max: 20, message: MSG.CONTENT_LIMITIATION }
        ]
      },
      formLabelWidth: '135px',
      data: {
        toolTypeName: null,
        toolName: null,
        toolCode: '',
        toolModel: '',
        initialLife: null,
        toolMaxLife: null,
        manufacturer: '',
        recordType: '', // 计数方式 1: 产量计数  2：时间计数
        timeRecordUnit: '' // 时长单位 1 秒  2 分  3 时
      }
    };
  },
  watch: {
    extendPropertyValueVoList(val, oldVal) {
      this.toExtendRulesAndShow();
    },
    showDialog(val, oldVal) {
      if (val) {
        this.clearData();
        this.toExtendRulesAndShow();
      }
    }
  },
  computed: {
    yieldKey() {
      return this.$store.state.pageInfo.pageConfigInfo.yieldKey;
    }
  },
  methods: {
    removeSpace(key) {
      this.data[key] = this.data[key].replace(/[^\d.]/g, '');
      this.data[key] = this.data[key].replace(/\s+/g, '');
    },
    change(e) {
      this.$forceUpdate();
    },
    /**
     * @description: 表单中扩展字段处理
     * @return: void
     */
    toExtendRulesAndShow() {
      this.showValidate = false;
      this.extendPropertyValueVoList.map((item) => {
        item.extendValue = null;
        return item;
      });
      var rules = JSON.parse(JSON.stringify(this.rulesUpdate));
      for (var item in this.extendPropertyValueVoList) {
        // 若类型是日期
        if (this.extendPropertyValueVoList[item].propertyType === 3) {
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: ['blur', 'change'] }
          ];
        } else { // 若类型是字符或数字
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: 'blur' }
          ];
        }
      }
      this.rulesUpdate = rules;
      this.data.extendPropertyValueVoList = this.extendPropertyValueVoList;
      this.showValidate = true;
    },
    validateNotNegative(rule, value, callback) {
      if (value < 0) {
        callback(new Error(MSG.NOT_NEGATIVE));
      } else if (value > 999999) {
        callback(new Error(MSG.CONTENT_LIMITIATION));
      } else {
        callback();
      }
    },
    setSelected: function(value) {
    },

    cancelAction: function() {
      if (this.handleCancel) {
        this.clearData();
        this.handleCancel();
      }
    },
    confirmAction: function() {
      var self = this;
      if (this.handleConfirm) {
        setTimeout(function() {
          self.clearData();
        });
        this.data.initialLife = this.data.initialLife.replace(/[^\d.]/g, '');
        this.data.toolMaxLife = this.data.toolMaxLife.replace(/[^\d.]/g, '');
        this.$refs.updateForm.clearValidate();
        this.handleConfirm(this.data);
      }
    },
    clearData: function() {
      var cleanData = {
        toolTypeName: null,
        toolName: null,
        toolCode: '',
        toolModel: '',
        initialLife: null,
        toolMaxLife: null,
        manufacturer: '',
        extendPropertyValueVoList: JSON.parse(JSON.stringify(this.data.extendPropertyValueVoList))
      };
      cleanData.extendPropertyValueVoList.forEach(function(item) {
        item.extendValue = '';
      });
      this.data = cleanData;
    },
    closeAction: function() {
      if (this.handleClose) {
        this.handleClose();
      }
    },
    /**
     * @description: 选择工具类型
     * @param {object} data 选中工具
     * @return: void
     */
    handleToolTypeChange(data) {
      for (var j = 0; j < this.$store.state.filters.toolTypeList.length; j++) {
        if (this.$store.state.filters.toolTypeList[j].basicValue === data.toolTypeName) {
          data.toolTypeId = this.$store.state.filters.toolTypeList[j].basicId;
          break;
        }
      }
    },
    /**
     * @description: 检查必填项是否填了
     * @return: boolean
     */
    checkDataCorrect() {
      this.$refs.updateForm.validate((valid) => {
        if (valid) {
          this.confirmAction();
        }
      });
    }
  }
};
</script>
<style>
.create-tool .type{
    margin-bottom: 13px;
}

.create-tool .input-group {
  display: flex;
}

.create-tool .input-label-1{
  margin-right: 60px;
  line-height: 30px;
}

.create-tool .input{
  width: 60%;
}

.create-tool  .el-select {
  width: 100%;
}

.create-tool .el-input__icon {
  line-height: 30px;
}

.create-tool .input-label-2{
  margin-right: 30px;
  line-height: 30px;
}

/* common style may move out */
.create-tool .el-input__inner {
  height: 30px;
}
.create-tool .el-dialog__header{
    line-height: initial;
    text-align: initial;
    border-bottom: 1px solid grey;
}
.create-tool .el-dialog__body{
    line-height: initial;
    text-align: initial;
    max-height: 300px !important;
    overflow-y: auto;
}
.create-tool .el-dialog__footer{
    line-height: initial;
    text-align: center;
}
</style>
