<template>
  <el-dialog
    class="modify-tool"
    width="50%"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content">
      <el-row>
        <el-form
            ref="confirmForm"
            :label-width="formLabelWidth"
            :model="resData"
            :rules="rulesUpdate"
            class="">
           <!-- <el-form-item :label="EQUIPMENT_DATA.RECORS_DATA" prop="recordProductionCount">
              <el-input @input="change($event, resData.recordProductionCount)" placeholder v-model="resData.recordProductionCount" @keyup.native="removeSpace('recordProductionCount')"></el-input>
            </el-form-item> -->
            <el-form-item :label="EQUIPMENT_DATA.TOOL_START_AGE" prop="initialLife">
              <el-input @input="change($event, resData.initialLife)" placeholder v-model="resData.initialLife" @keyup.native="removeSpace('initialLife')"></el-input>
            </el-form-item>
            <el-form-item :label="EQUIPMENT_DATA.TOOL_MAX_AGE" prop="toolMaxLife">
              <el-input @input="change($event, resData.toolMaxLife)" placeholder v-model="resData.toolMaxLife" @keyup.native="removeSpace('toolMaxLife')"></el-input>
            </el-form-item>
            <div v-if="extendPropertyValueVoList && extendPropertyValueVoList.length>0">
              <div v-for="(item,index) in extendPropertyValueVoList" :key="index">
                <el-form-item :class="{'requiredStar':item.propertyIsNull===1}" :label="item.extendName" :label-width="formLabelWidth" :prop="'extendPropertyValueVoList.'+index+'.extendValue'" :rules="rulesUpdate[item.propertyCode]" >
                  <el-input v-if="item.propertyType!==3" v-model="item.extendValue" @input="change($event)"></el-input>
                  <el-date-picker
                    @input="change($event)"
                    v-else
                    v-model="item.extendValue"
                    type="datetime"
                    :format="FORM_CONFIG_DATE[item.propertyLength]"
                    :value-format="FORM_CONFIG_DATE[item.propertyLength]"
                  ></el-date-picker>
                </el-form-item>
              </div>
            </div>
        </el-form>
      </el-row>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="checkDataCorrect">{{confirmLabel}}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
    </div>
  </el-dialog>
</template>
<script>
// 文字常量
import { EQUIPMENT_DATA, BTN_TEXT, MSG, FORM_CONFIG_DATE } from '@/utils/constantsFile.js';
export default {
  name: 'ModifyToolModal',
  props: {
    // 模态框名字
    title: { type: String, default: '' },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function,
    // 要修改的工具数据
    resOrigData: { type: Object, default: () => {} }
  },
  data() {
    var setValidateForm = (rule, value, callback) => {
      return this.$options.filters.formatterFormValidate(rule, value, callback, this.extendPropertyValueVoList);
    };
    return {
      resData: {
        // recordProductionCount: null,
        initialLife: null,
        toolMaxLife: null
      },
      setValidateForm: setValidateForm,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      MSG: MSG,
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      formLabelWidth: '135px',
      rulesUpdate: {
        recordProductionCount: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 1, max: 6, message: MSG.CONTENT_LIMITIATION, trigger: 'blur' }],
        initialLife: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 1, max: 6, message: MSG.CONTENT_LIMITIATION, trigger: 'blur' }],
        toolMaxLife: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 1, max: 6, message: MSG.CONTENT_LIMITIATION, trigger: 'blur' }]
      },
      // 扩展字段
      extendPropertyValueVoList: []
    };
  },
  watch: {
    resOrigData(val, oldVal) {
      this.initData();
      // this.$refs.confirmForm.clearValidate();
    }
  },
  methods: {
    removeSpace(key) {
      this.resData[key] = this.resData[key].replace(/[^\d.]/g, '');
      this.resData[key] = this.resData[key].replace(/\s+/g, '');
    },
    change(e, data) {
      this.$forceUpdate();
    },
    initData() {
      this.resData = JSON.parse(JSON.stringify(this.resOrigData));
      this.resData.recordProductionCount = this.resData.recordProductionCount.toString();
      this.resData.initialLife = this.resData.initialLife.toString();
      this.resData.toolMaxLife = this.resData.toolMaxLife.toString();
      this.toExtendRulesAndShow(this.resData);
    },
    checkDataCorrect() {
      var flag = false;
      var self = this;
      this.$refs.confirmForm.validate((valid) => {
        if (valid) {
          flag = true;
        }
      });
      for (var key in this.$refs.confirmForm.rules) {
        var required = false;
        this.$refs.confirmForm.rules[key].forEach(function(item) {
          if (item.required) {
            required = item.required;
          }
        });
        if (flag && required && this.resData[key].replace(/\s+/g, '').length === 0) {
          flag = false;
        }
      }
      if (flag) {
        self.confirmAction();
      }
    },
    cancelAction: function() {
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    confirmAction: function() {
      if (this.handleConfirm) {
        this.handleConfirm(this.resData);
      }
    },
    closeAction: function() {
      if (this.handleClose) {
        this.handleClose();
      }
    },
    /**
     * @description: 表单中扩展字段处理
     * @return: void
     */
    toExtendRulesAndShow(data) {
      // 保存扩展字段数据
      var arr = data.extendPropertyValueVoList.map((item) => {
        item.relationshipTypeId = data.toolId;
        item.extendValue = item.extendValue ? item.extendValue : null;
        return item;
      });
      this.extendPropertyValueVoList = arr;
      var rules = JSON.parse(JSON.stringify(this.rulesUpdate));
      for (var item in this.extendPropertyValueVoList) {
        // 若类型是日期
        if (this.extendPropertyValueVoList[item].propertyType === 3) {
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: ['blur', 'change'] }
          ];
        } else { // 若类型是字符或数字
          rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: 'blur' }
          ];
        }
      }
      this.rulesUpdate = rules;
    }
  }
};
</script>
<style>
.modify-tool .type{
    margin-bottom: 13px;
}

.modify-tool .input-group {
  display: flex;
}

.modify-tool .input-label-1{
  width: 100px;
  line-height: 30px;
}

.modify-tool .input-content{
  width: 80%;
  float: right;
}

/* common style may move out */
.modify-tool .el-input__inner {
  height: 30px;
}
.modify-tool .el-dialog__header{
    line-height: initial;
    text-align: initial;
    border-bottom: 1px solid grey;
}
.modify-tool .el-dialog__body{
    line-height: initial;
    text-align: initial;
}
.modify-tool .el-dialog__footer{
    line-height: initial;
    text-align: center;
}
.requiredStar /deep/ label:before {
    content: '*';
    color: #F56C6C;
    margin-right: 4px;
  }
</style>
