import { EQUIPMENT_DATA, BTN_TEXT, STATUS_CODE } from '@/utils/constantsFile';
import { getPageToolRecordByToolId } from '@/api/equipment';
// 引入警告信息
import { showWarningMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
// 导入日期处理类库
import moment from 'moment';
export default {
  components: {
  },
  props: {
    // 工具ID
    toolId: { type: String, default: '' }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      // 表格加载控制标识
      tableLoading: false,
      data: [],
      tablePage: {
        currentPage: 1,
        pageSize: 10,
        total: 0,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        perfect: true,
        layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
      },
      tableColumn: [
        // 操作原因
        {
          field: 'typeName',
          title: EQUIPMENT_DATA.TOOL_HANDLE_REASON,
          align: 'center'
        },
        // 启用时间
        {
          field: 'startTime',
          title: EQUIPMENT_DATA.TURN_ON_TIME,
          align: 'center',
          formatter({ cellValue, row, column }) {
            if (cellValue) {
              return moment(cellValue).format('YYYY-MM-DD HH:mm:ss');
            } else {
              return '';
            }
          }
        },
        {
          field: 'toolName',
          title: EQUIPMENT_DATA.TOOL_NAME,
          align: 'center'
        },
        {
          field: 'toolTypeName',
          title: EQUIPMENT_DATA.TOOL_TYPE,
          align: 'center'
        },
        {
          field: 'recordProduction',
          title: EQUIPMENT_DATA.TOTAL_OUTPUT,
          align: 'center'
        },
        {
          field: 'initialLife',
          title: EQUIPMENT_DATA.TOOL_START_AGE,
          align: 'center',
          formatter: ({ row }) => {
            return this.tranfromTime(row, 'initialLife');
          }
        },
        {
          field: 'maxLife',
          title: EQUIPMENT_DATA.TOOL_MAX_AGE,
          align: 'center',
          formatter: ({ row }) => {
            return this.tranfromTime(row, 'maxLife');
          }
        },
        {
          field: 'remainingLife',
          title: EQUIPMENT_DATA.TOOL_REMAIN_AGE,
          align: 'center',
          formatter: ({ row }) => {
            return this.tranfromTime(row, 'remainingLife');
          }
        },
        {
          field: 'useCount',
          title: EQUIPMENT_DATA.USED_TIME,
          align: 'center',
          formatter: ({ row }) => {
            return this.tranfromTime(row, 'useCount');
          }
        },
        {
          field: 'endTime',
          title: BTN_TEXT.ENDTIME,
          align: 'center',
          formatter({ cellValue, row, column }) {
            if (cellValue) {
              return moment(cellValue).format('YYYY-MM-DD HH:mm:ss');
            } else {
              return '';
            }
          }
        },
        {
          field: 'totalTime',
          title: EQUIPMENT_DATA.USE_DURATION,
          align: 'center',
          formatter: this.$options.filters.formatDifferentTime
        },
        {
          field: 'realName',
          title: EQUIPMENT_DATA.HANDLE_USER,
          align: 'center'
        }
      ]
    };
  },
  methods: {
    /**
   * @description 根据时间单位  转换数据
  */
    tranfromTime(row, name) {
      let count = '';
      if (row.recordType === 2) {
        if (row.timeRecordUnit === 1) {
          count = row[name];
        } else if (row.timeRecordUnit === 2) {
          count = (row[name] / 60).toFixed(2);
        } else if (row.timeRecordUnit === 3) {
          count = (row[name] / 3600).toFixed(2);
        };
      } else {
        count = row[name];
      }
      return count;
    },
    /**
     * @description: 初始化界面获取设备历史数据
     * @return: void
    */
    initHistoryData() {
      var self = this;
      this.tableLoading = true;
      var params = {
        current: this.tablePage.currentPage,
        size: this.tablePage.pageSize,
        toolId: this.toolId
      };
      getPageToolRecordByToolId(params).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          self.data = res.rows;
          self.tablePage.total = res.total;
          self.tableLoading = false;
          self.data.forEach(function(item) {
            if (!item.startTime || !item.endTime) {
              item.totalTime = 0;
            } else {
              var t1 = moment(item.startTime);
              var t2 = moment(item.endTime);
              item.totalTime = t2.diff(t1, 'seconds');
            }
          });
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 处理表翻页
     * @param {int} currentPage 当前页码
     * @param {int} pageSize 页面展示条数
     * @return: void
    */
    handlePageChange({ currentPage, pageSize }) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.initHistoryData();
    },
    /**
     * @description:  页面返回
     * @param {object} data 第几页
     * @return: void
     */
    goBack(data) {
      var res = {
        page: 'toolhistory',
        data: data
      };
      this.$emit('goBack', res);
    }
  },
  created() {
    this.initHistoryData();
  }
};
