// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import ZTree from '@/components/common/ZTree/ZTree';
import { EQUIPMENT_DATA, PAGE_TITLE, BTN_TEXT, ENERGY, STATUS_CODE } from '@/utils/constantsFile';
import { deviceWorkshopTypeGetTreeAction, deviceGroupGetTreeAction, getPageDeviceByCondition } from '@/api/management.js';
// 引入警告信息
import { showWarningMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem,
    ZTree
  },
  props: {
  },
  data() {
    return {
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      PAGE_TITLE: PAGE_TITLE,
      BTN_TEXT: BTN_TEXT,
      ENERGY: ENERGY,
      typeSelect: '1',
      // 选择的设备过滤ids
      selectDeviceFilter: [],
      nodeData: [],
      toolListTable: {
        page: {
          currentPage: 1,
          pageSize: 10,
          total: 30,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
        },
        columns: [
          {
            field: 'deviceName',
            title: ENERGY.DEVICENAME,
            align: 'center'
          }, {
            field: 'devicePriCode',
            title: ENERGY.DEVICECODE,
            align: 'center'
          }, {
            field: 'factory',
            title: ENERGY.FACTORY,
            align: 'center'
          }, {
            field: 'workshop',
            title: ENERGY.WORKSHOP,
            align: 'center'
          }, {
            field: 'typeName',
            title: PAGE_TITLE.DEVICE_TYPE,
            align: 'center'
          }, {
            field: 'groupName',
            title: ENERGY.GROUPNAME,
            align: 'center'
          }
        ],
        data: []
      },
      selectData: ''
    };
  },
  methods: {
    /**
     * @description: 初始化转移界面
     * @return void
    */
    initPage() {
      this.getTreeBydeviceGroup();
      // this.getDeviceListByTree(this.typeSelect);
    },
    /**
     * @description: 切换tab页面
     * @param {string} tabid
     * @return void
    */
    switchTab(tabid) {
      this.typeSelect = tabid;
      if (tabid === '1') {
        this.getTreeBydeviceGroup();
      } else {
        this.getTreeBydeviceWorkshopType();
      }
    },
    /**
     * @description: 根据车间分类获取设备
     * @return void
    */
    getTreeBydeviceGroup() {
      deviceGroupGetTreeAction().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nodeData = res.rows;
          for (var i = 0; i < this.nodeData.length; i++) {
            if (this.nodeData[i].code) {
              this.selectData = this.nodeData[i];
              break;
            }
          }
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 根据设备分类获取设备
     * @return void
    */
    getTreeBydeviceWorkshopType() {
      deviceWorkshopTypeGetTreeAction().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nodeData = res.rows;
          for (var i = 0; i < this.nodeData.length; i++) {
            if (this.nodeData[i].code) {
              this.selectData = this.nodeData[i];
              break;
            }
          }
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 获取设备列表
     * @param {string} type: 类型(groupIdList,typeIdList)
     * @return void
    */
    getDeviceListByTree(type) {
      var params = {
        current: this.toolListTable.page.currentPage,
        size: this.toolListTable.page.pageSize
      };
      if (type === '1') {
        params.groupIdList = this.selectDeviceFilter;
      }
      if (type === '2') {
        params.typeIdList = this.selectDeviceFilter;
      }
      getPageDeviceByCondition(params).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.toolListTable.data = res.rows;
          this.toolListTable.page.total = res.total;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 点击树结构元素
     * @param {object} data 树结构渲染数据
     * @param {object} treeNode 树结构真实数据
     * @return void
    */
    handleNodeClick(events, nodes, obj) {
      this.selectData = nodes.name;
      this.selectDeviceFilter = [];
      this.selectDeviceFilter = this.getChildren(this.selectDeviceFilter, nodes);
      this.toolListTable.page.currentPage = 1;
      this.getDeviceListByTree(this.typeSelect);
    },
    /**
     * @description: 递归获取子级数据
     * @param row 行数据
     * @return: void
     */
    getChildren(ids, treeNode) {
      ids.push(treeNode.id);
      if (treeNode.isParent) {
        for (var obj in treeNode.children) {
          this.getChildren(ids, treeNode.children[obj]);
        }
      }
      return ids;
    },
    /**
     * @description: 执行table分页
     * @param {string} data 页面信息
     * @return: void
     */
    handleToolListTablePageChange(data) {
      this.toolListTable.page.currentPage = data.currentPage;
      this.toolListTable.page.pageSize = data.pageSize;
      this.getDeviceListByTree(this.typeSelect);
    },
    /**
     * @description:tablb 点击事件
     * @param {object} data 第几页
     * @return: void
     */
    clicktable(data) {
      this.goBack(data);
    },
    /**
     * @description:  页面返回
     * @param {object} data 第几页
     * @return: void
     */
    goBack(data) {
      var res = {
        page: 'movetool',
        data: data
      };
      this.$emit('goBack', res);
    }
  },
  created() {
    this.initPage();
  }
};
